




















import Vue from 'vue';

import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebaseui/dist/firebaseui.css'; // Use FirebaseUI button component
import UserApi from '@/apis/UserApi';
import GoogleClient from '@/libs/GoogleClient';

export default Vue.extend({
  name: 'Login',
  async created() {
    if (GoogleClient.currentUser && firebase.auth().currentUser && await UserApi.isAdmin()) {
      // auto redirect to homepage if already logged in and accidentally visit login page
      if (this.$router.currentRoute.path !== this.$store.state.homepageRoute) {
        this.$router.push(this.$store.state.homepageRoute);
      }
    }
  },
  methods: {
    googleSignin() {
      GoogleClient.signIn();
    },
  },
});
